.title-icon {
  position: relative;
  margin-top: 100px;
  margin-bottom: 75px;

  &.small-margin-top {
    margin-top: 60px;
  }

  &.with-padding {
    margin: 0;
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .content-area {
    text-align: center;

    &.smaller {
      width: 625px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
    }

    .icon-area {
      img {
        width: 47px;
        margin: 0 auto 16px;
      }
    }

    h2 {
      text-align: center;
      font-family: 'Crimson Text';
      font-weight: bold;
      font-size: 45px;
      line-height: 1.2;
      color: #000;
    }

    p {
      font-family: 'Open Sans';
      font-size: 20px;
      line-height: 1.5;
      color: #4D4D4D;
      width: 750px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 0;

      &.semibold {
        font-weight: 600;
      }

      a {
        color: #4C721D;
        text-decoration: underline;

        &.no-underline {
          text-decoration: none;
        }
      }
    }

    .btn-green {
      padding: 5px 20px 10px;
    }
  }
}

@media (max-width: 767.98px) {
  .title-icon {
    margin-top: 30px;
    margin-bottom: 30px;

    &.small-margin-top {
      margin-top: 30px;
    }

    .content-area {

      .icon-area {
        img {
          width: 30px;
        }
      }

      h2 {
        font-size: 30px;
      }
    }
  }
}