.half-text-image-box {
  position: relative;
  margin-top: 80px;
  margin-bottom: 80px;

  .content-area {
    width: 1200px;
    max-width: 100%;
    box-shadow: 0 0 15px rgba(0, 0, 0, .1);
    padding: 60px 70px 60px 85px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 767.98px) {
      padding: 15px;
    }

    .text-area {
      flex: 0 0 38%;
      max-width: 38%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      h2 {
        font-family: 'Crimson Text';
        font-weight: 600;
        font-size: 45px;
        line-height: 1.2;

        @media (max-width: 767.98px) {
          font-size: 34px;
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.5;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }
    }

    .image-area {
      flex: 0 0 56%;
      max-width: 56%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-top: 20px;
      }

      img {
        border-radius: 25px;
      }
    }
  }
}