.half-text {
  position: relative;
  margin-top: 80px;
  margin-bottom: 80px;

  .content-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .half-box {
      flex: 0 0 41.5%;
      max-width: 41.5%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 35px;
      }

      h2 {
        font-family: 'Crimson Text';
        font-weight: 600;
        font-size: 45px;
        line-height: 1.2;

        @media (max-width: 767.98px) {
          font-size: 34px;
        }

        &.dropdown-title {

          a {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 15px;
            border-bottom: 1px solid #999;
            width: 100%;
            color: inherit;

            &:not(.collapsed)::after {
              transform: scale(-1);
              transition: all .35s;
            }

            &::after {
              content: "";
              width: 55px;
              height: 55px;
              background-image: url(../../images/dropdown-icon.png);
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
              transition: all .35s;
            }
          }

        }
      }

      p,
      ul {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.5;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }
    }
  }
}