.text-image-half-large {
  position: relative;
  margin-bottom: 140px;

  @media (max-width: 991.98px) {
    margin-bottom: 70px;
  }

  &.image-right {
    .content-area {
      flex-direction: row-reverse;

      .text-area {
        justify-content: flex-end;
        display: flex;
      }
    }
  }

  .content-area {
    width: 1800px;
    max-width: 100%;
    padding: 0 15px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    .image-area {
      flex: 0 0 58%;
      max-width: 58%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 20px;
      }

      .img-box {
        width: 100%;
        padding-top: 67%;
        position: relative;

        img {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          object-fit: cover;
          object-position: center;
          border-radius: 10px;
        }
      }
    }

    .text-area {
      flex: 0 0 37%;
      max-width: 37%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      .text-box {
        width: 390px;
        max-width: 100%;

        @media (max-width: 991.98px) {
          width: 100%;
        }

        h3 {
          font-family: 'Crimson Text';
          font-weight: 600;
          font-size: 40px;
          line-height: 1.2;
          color: #000;
          margin-bottom: 15px;
          padding-right: 10%;

          @media (max-width: 767.98px) {
            font-size: 28px;
          }
        }

        p {
          font-family: 'Open Sans';
          font-size: 20px;
          line-height: 1.8;
          color: #4D4D4D;

          @media (max-width: 767.98px) {
            font-size: 16px;
          }
        }

        .btn-green {
          font-family: 'Crimson Text';
          font-weight: 600;
          font-size: 20px;
          line-height: 1.2;
          color: #fff;
          padding: 5px 5px 10px;
          background-color: #4C721D;
          border: 1px solid #4C721D;
          min-width: 175px;
          min-height: 56px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          margin-top: 15px;
          border-radius: 50px;

          &:hover {
            color: #4C721D;
            background-color: #fff;
          }
        }
      }
    }
  }
}