.page-banner {
  position: relative;
  padding: 110px 0;
  background-size: cover;
  background-position: center right;

  .content-area {
    width: 775px;
    max-width: 100%;

    &.larger {
      width: 800px;
    }

    &.medium {
      width: 725px;
    }

    &.smaller p {
      width: 645px;
      max-width: 100%;
    }

    h1 {
      font-family: 'Crimson Text';
      font-weight: bold;
      font-size: 60px;
      line-height: 1.2;
      color: #fff;
      margin-bottom: 20px;
    }

    p {
      font-family: 'Open Sans';
      font-weight: 600;
      font-size: 20px;
      line-height: 1.8;
      color: #fff;
      margin-bottom: 40px;

      a {
        color: inherit;
        text-decoration: underline;
      }
    }
  }
}

@media (max-width: 767.98px) {
  .page-banner {
    padding: 60px 0;
    background-position: center left;

    .content-area {

      h1 {
        font-size: 40px;
        margin-bottom: 15px;
      }

      p {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 15px;
      }
    }
  }
}