.vegetable-transplants-content {
  position: relative;
  margin-top: 75px;
  margin-bottom: 50px;

  @media (max-width: 767.98px) {
    margin-top: 35px;
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (max-width: 1199.98px) {
      flex-direction: column-reverse;
    }

    .left-area {
      flex: 0 0 52%;
      max-width: 52%;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      .extras {
        padding-right: 20%;

        @media (max-width: 767.98px) {
          padding-right: 0;
        }
      }

      .delivery-pickup {
        &>p {
          padding-right: 10%;

          @media (max-width: 767.98px) {
            padding-right: 0;
          }
        }

        .delivery-options-area {
          margin-top: 35px;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;

          .delivery-box {
            flex: 0 0 48%;
            max-width: 48%;
            position: relative;
            padding-left: 68px;
            margin-bottom: 35px;

            @media (max-width: 767.98px) {
              flex: 0 0 100%;
              max-width: 100%;
            }

            .icon-area {
              position: absolute;
              left: 0;
              top: 0;

              img {
                max-width: 60px;
                object-fit: contain;
              }
            }
          }
        }
      }

      h3 {
        margin-top: 100px;
        margin-bottom: 10px;
        font-family: "Crimson Text";
        font-weight: 600;
        font-size: 45px;
        line-height: 1.2;
        color: #000;

        @media (max-width: 767.98px) {
          margin-top: 50px;
          font-size: 36px;
        }
      }

      h4 {
        font-family: 'Open Sans';
        font-weight: bold;
        font-size: 25px;
        line-height: 1.2;
        color: #4D4D4D;
        margin-bottom: 5px;

        @media (max-width: 767.98px) {
          font-size: 20px;

          br {
            display: none;
          }
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.8;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
          margin-bottom: 0;
        }

        a {
          color: #4C721D;
          font-weight: 600;

          &.text-underline {
            text-decoration: underline;
          }
        }
      }

      .btn-green {
        padding: 5px 25px;
        margin-top: 30px;
      }
    }

    .right-area {
      flex: 0 0 40%;
      max-width: 40%;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      .calculator-box {
        background: #455A21;
        box-shadow: 0 0 30px rgba(0, 0, 0, .2);
        text-align: center;
        padding: 30px 35px;
        position: sticky;
        top: 0;

        h3 {
          font-family: 'Crimson Text';
          font-weight: 600;
          font-size: 40px;
          line-height: 1.2;
          color: #fff;
          margin-bottom: 10px;

          @media (max-width: 767.98px) {
            margin-bottom: 15px;
          }
        }

        p {
          color: #fff;
          font-size: 12px;
          line-height: 1.2;
          margin-bottom: 10px;
        }

        form {
          margin-top: 35px;

          table {
            margin-left: auto;
            margin-right: auto;

            tr {
              td {
                color: #fff;
                font-family: 'Open Sans';
                font-size: 18px;

                @media (max-width: 767.98px) {
                  width: 100%;
                  display: block;
                }

                &.pt-2 {
                  padding-top: 5px;
                }

                input[type=text] {
                  font-family: 'Open Sans';
                  color: #231F20;
                  font-size: 17px;
                  padding-left: 17px;
                  width: 100%;
                  height: 50px;
                  border: 1px solid #aeaeae;
                  margin-bottom: 5px;
                }

                input[type=button] {
                  font-family: 'Crimson Text';
                  color: #4C721D;
                  font-weight: 600;
                  font-size: 16px;
                  width: 100%;
                  height: 43px;
                  display: inline-flex;
                  align-items: center;
                  justify-content: center;
                  background-color: #fff;
                  border-radius: 50px;
                  border: none;

                  @media (max-width: 767.98px) {
                    margin-bottom: 20px;
                  }

                  &:disabled {
                    pointer-events: none;
                    background-color: #aeaeae;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}