.page-selector {
  position: relative;
  margin-bottom: 75px;
  background-color: #F2F2F2;

  &.no-margin-bottom {
    margin-bottom: 0;
  }

  &::after {
    content: "";
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    background: #F2F2F2;
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    a {
      font-family: 'Open Sans';
      font-weight: bold;
      font-size: 19px;
      line-height: 1;
      color: #383838;
      padding: 20px 15px;

      &.active,
      &:hover {
        color: #fff;
        background-color: #455A21;
      }
    }
  }
}

@media (max-width: 767.98px) {
  .page-selector {
    position: relative;
    margin-bottom: 35px;
    background-color: #F2F2F2;

    .content-area {

      a {
        width: 100%;
        text-align: center;
      }
    }
  }
}