.round-image-text {
  position: relative;
  margin-top: 100px;
  margin-bottom: 100px;

  &.padding-no-margin {
    margin: 0;
    padding-bottom: 100px;
  }

  @media (max-width: 767.98px) {
    margin-top: 50px;
    margin-bottom: 50px;

    &.padding-no-margin {
      padding-bottom: 50px;
    }
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .image-area {
      flex: 0 0 20%;
      max-width: 20%;

      @media (max-width: 767.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        padding: 0 20%;
        margin-bottom: 30px;
      }

      .image-box {
        width: 100%;
        padding-top: 100%;
        position: relative;

        img {
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          border-radius: 50%;
          position: absolute;
          object-fit: cover;
          object-position: center;
        }
      }
    }

    .text-area {
      flex: 0 0 75%;
      max-width: 75%;

      @media (max-width: 767.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      h3 {
        font-family: 'Crimson Text';
        font-size: 45px;
        font-weight: 600;
        line-height: 1.2;
        margin-bottom: 10px;

        @media (max-width: 767.98px) {
          font-size: 30px;
        }
      }

      .meta-link {
        a {
          font-family: 'Open Sans';
          font-size: 20px;
          line-height: 1.2;
          color: #4D4D4D;
          margin-right: 30px;
          position: relative;
          display: inline-flex;
          align-items: center;
          margin-bottom: 30px;

          @media (max-width: 991.98px) {
            margin-bottom: 15px;
          }

          &.location {
            color: #4C721D;
            text-decoration: underline;

            &::before {
              content: "";
              width: 25px;
              height: 35px;
              background-image: url(../../images/icon-location.png);
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
              margin-right: 10px;
              display: block;
            }
          }

          &.telp {

            &::before {
              content: "";
              width: 25px;
              height: 35px;
              background-image: url(../../images/icon-telp.png);
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
              margin-right: 10px;
              display: block;
            }
          }

          &.fax {

            &::before {
              content: "";
              width: 25px;
              height: 35px;
              background-image: url(../../images/icon-fax.png);
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
              margin-right: 10px;
              display: block;
            }
          }
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.5;
        color: #4D4D4D;
        margin-bottom: 30px;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }
    }
  }
}