.table-specs {
  position: relative;
  width: 1180px;
  max-width: 100%;
  margin: 100px auto;

  @media (max-width: 767.98px) {
    margin: 30px auto;
  }

  table {
    thead {
      tr {
        th {
          font-family: 'Open Sans';
          font-weight: bold;
          font-size: 20px;
          line-height: 1.5;
          color: #fff;
          text-align: center;
          vertical-align: middle;
          background-color: #4C721D;

          @media (max-width: 767.98px) {
            font-size: 12px;
          }
        }
      }
    }

    tbody {
      tr {
        &:nth-child(even) {
          background-color: #C7C7C8;
        }

        td {
          font-family: 'Open Sans';
          font-weight: normal;
          font-size: 20px;
          line-height: 1.5;
          color: #4D4D4D;
          padding: 15px 15px 15px 30px;

          @media (max-width: 767.98px) {
            font-size: 12px;
            padding: 5px;
          }

          &:first-child,
          &:last-child {
            text-align: center;
          }
        }
      }
    }
  }
}