.half-table {
  position: relative;
  padding-top: 80px;
  padding-bottom: 20px;

  @media (max-width: 767.98px) {
    padding-top: 40px;
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .half-table-box {
      flex: 0 0 42%;
      max-width: 42%;
      margin-bottom: 100px;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 50px;
      }

      @media (max-width: 767.98px) {
        margin-bottom: 30px;
      }

      h3 {
        font-family: 'Crimson Text';
        font-weight: 600;
        font-size: 45px;
        line-height: 1.1;

        small {
          font-size: 30px;
          top: -10px;
          position: relative;
        }

        @media (max-width: 767.98px) {
          font-size: 36px;
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.5;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }

      .table-strip-box {
        margin-top: 20px;
      }
    }
  }
}