.event-info {
  position: relative;
  margin-top: 70px;
  margin-bottom: 90px;

  @media (max-width: 767.98px) {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .content-area {
    width: 1040px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;

    .event-title {
      text-align: center;
      margin-bottom: 70px;

      @media (max-width: 767.98px) {
        margin-bottom: 30px;
      }

      .icon-area {
        margin-bottom: 15px;

        img {
          width: 45px;
        }
      }

      h2 {
        font-family: 'Crimson Text';
        font-weight: bold;
        font-size: 45px;
        line-height: 1.2;
        color: #000;
        margin-bottom: 10px;

        @media (max-width: 767.98px) {
          font-size: 30px;
        }
      }

      p {
        font-family: 'Open Sans';
        font-weight: 600;
        font-size: 20px;
        line-height: 1.5;
        color: #4D4D4D;
        margin-bottom: 10px;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }

      a.location {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.5;
        color: #4C721D;
        text-decoration: underline;
        display: inline-flex;
        align-items: center;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }

        &::before {
          content: "";
          width: 25px;
          height: 33px;
          background-image: url(../../images/icon-location.png);
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
          margin-right: 10px;
        }
      }
    }

    p {
      font-family: 'Open Sans';
      font-weight: 400;
      font-size: 20px;
      line-height: 1.5;
      color: #4D4D4D;
      margin-bottom: 30px;

      @media (max-width: 767.98px) {
        font-size: 16px;
        margin-bottom: 20px;
      }

      a {
        color: #4C721D;
        text-decoration: underline;
      }
    }
  }
}