.half-text-table {
  position: relative;
  margin-top: 60px;
  margin-bottom: 75px;

  @media (max-width: 767.98px) {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .text-area {
      flex: 0 0 42%;
      max-width: 42%;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      h3 {
        font-family: 'Crimson Text';
        font-weight: 600;
        font-size: 45px;
        line-height: 1.2;
        color: #000000;

        @media (max-width: 767.98px) {
          font-size: 36px;
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.5;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }

    }

    .table-area {
      flex: 0 0 42%;
      max-width: 42%;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-top: 20px;
      }
    }
  }
}