header {
  position: relative;
  background-color: #fff;
  padding-top: 30px;
  padding-bottom: 30px;
  min-height: 173px;

  &.transparent-header {
    margin-bottom: -173px;
    z-index: 2;
    background-color: transparent;

    .content-area .logo-area a img {
      filter: brightness(1000);
    }

    .content-area .menu-area ul li a {
      color: #fff;
    }
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    .logo-area {
      flex: 0 0 30%;
      max-width: 30%;

      a {
        img {
          width: 185px;
        }
      }
    }

    .menu-area {
      flex: 0 0 70%;
      max-width: 70%;
      margin: 0;

      ul {
        list-style: none;
        padding-left: 0;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin: 0;

        li {
          margin: 15px;

          &:last-child {
            margin-right: 0;
          }

          a {
            font-family: 'Crimson Text';
            font-size: 24px;
            line-height: 1.2;
            font-weight: bold;
            color: #383838;
            margin-bottom: 0;
          }
        }
      }
    }
  }
}

@media (max-width: 767.98px) {
  header {
    padding-top: 15px;
    padding-bottom: 15px;
    min-height: unset;

    .content-area {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;

      .logo-area {
        flex: 0 0 30%;
        max-width: 30%;

        a {
          img {
            width: 100px;
          }
        }
      }

      .mobile-menu-toggle img {
        filter: brightness(0) saturate(100%) invert(36%) sepia(19%) saturate(1624%) hue-rotate(45deg) brightness(99%) contrast(86%);
      }

      .menu-area {
        flex: 0 0 100%;
        max-width: 100%;
        margin: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: 0;
        pointer-events: none;
        transition: all .35s;
        z-index: 2;

        &.active {
          opacity: 1;
          pointer-events: all;
          transition: all .35s;
        }

        .close-menu {
          position: absolute;
          top: 33px;
          right: 15px;
          filter: brightness(0) saturate(100%) invert(31%) sepia(100%) saturate(337%) hue-rotate(45deg) brightness(99%) contrast(90%);
        }

        ul {
          flex-direction: column;
          padding-top: 80px;

          li {
            margin: 10px 0;

            &:last-child {
              margin-right: 0;
            }

            a {
              font-family: 'Crimson Text';
              font-size: 24px;
              line-height: 1.2;
              font-weight: bold;
              color: #383838;
              margin-bottom: 0;
            }
          }
        }
      }
    }

    &.transparent-header {
      margin-bottom: -90.99px;
      z-index: 2;
      background-color: transparent;

      .content-area .logo-area a img {
        filter: brightness(1000);
      }

      .content-area .menu-area ul li a {
        color: #4c721d;
      }

      .content-area {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;

        .logo-area {
          flex: 0 0 30%;
          max-width: 30%;

          a {
            img {
              width: 100px;
            }
          }
        }

        .mobile-menu-toggle img {
          filter: none;
        }

        .menu-area {
          flex: 0 0 100%;
          max-width: 100%;
          margin: 0;
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: #fff;
          opacity: 0;
          pointer-events: none;
          transition: all .35s;
          z-index: 2;

          &.active {
            opacity: 1;
            pointer-events: all;
            transition: all .35s;
          }

          .close-menu {
            position: absolute;
            top: 33px;
            right: 15px;
            filter: brightness(0) saturate(100%) invert(31%) sepia(100%) saturate(337%) hue-rotate(45deg) brightness(99%) contrast(90%);
          }

          ul {
            flex-direction: column;
            padding-top: 80px;

            li {
              margin: 10px 0;

              &:last-child {
                margin-right: 0;
              }

              a {
                font-family: 'Crimson Text';
                font-size: 24px;
                line-height: 1.2;
                font-weight: bold;
                color: #383838;
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }

  }
}