.holiday-poinsettias-content {
  position: relative;
  margin-top: 75px;
  margin-bottom: 75px;

  @media (max-width: 767.98px) {
    margin-top: 35px;
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (max-width: 1199.98px) {
      flex-direction: column-reverse;
    }

    .left-area {
      flex: 0 0 47.5%;
      max-width: 47.5%;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      .collapse-area {
        margin-top: 125px;

        .collapse-box {
          padding-bottom: 30px;

          h2 {
            font-family: 'Crimson Text';
            font-weight: 600;
            font-size: 45px;
            line-height: 1.2;

            @media (max-width: 767.98px) {
              font-size: 34px;
            }

            &.dropdown-title {

              a {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-bottom: 15px;
                border-bottom: 1px solid #999;
                width: 100%;
                color: inherit;

                &:not(.collapsed)::after {
                  transform: scale(-1);
                  transition: all .35s;
                }

                &::after {
                  content: "";
                  width: 55px;
                  height: 55px;
                  background-image: url(../../images/dropdown-icon.png);
                  background-size: contain;
                  background-repeat: no-repeat;
                  background-position: center;
                  transition: all .35s;
                }
              }

            }
          }

          p,
          ul {
            font-family: 'Open Sans';
            font-size: 20px;
            line-height: 1.5;
            color: #4D4D4D;

            @media (max-width: 767.98px) {
              font-size: 16px;
            }
          }
        }
      }

      h3 {
        margin-top: 100px;
        margin-bottom: 10px;
        font-family: "Crimson Text";
        font-weight: 600;
        font-size: 45px;
        line-height: 1.2;
        color: #000;

        @media (max-width: 767.98px) {
          margin-top: 50px;
          font-size: 36px;
        }
      }

      h4 {
        font-family: 'Open Sans';
        font-weight: bold;
        font-size: 25px;
        line-height: 1.2;
        color: #4D4D4D;
        margin-bottom: 5px;

        @media (max-width: 767.98px) {
          font-size: 20px;

          br {
            display: none;
          }
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.8;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
          margin-bottom: 0;
        }

        a {
          color: #4C721D;
          font-weight: 600;

          &.text-underline {
            text-decoration: underline;
          }
        }
      }

      .btn-green {
        padding: 5px 25px;
        margin-top: 30px;
      }
    }

    .right-area {
      flex: 0 0 40%;
      max-width: 40%;

      @media (max-width: 1199.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      .order-online {
        background: #455A21;
        box-shadow: 0 0 30px rgba(0, 0, 0, .2);
        text-align: center;
        padding: 30px 35px;
        position: sticky;
        top: 0;

        @media (max-width: 767.98px) {
          padding: 15px;
        }

        h3 {
          font-family: 'Crimson Text';
          font-weight: 600;
          font-size: 40px;
          line-height: 1.2;
          color: #fff;
          margin-bottom: 10px;

          @media (max-width: 767.98px) {
            margin-bottom: 15px;
          }
        }

        .btn-white {
          font-family: 'Crimson Text';
          font-weight: 600;
          font-size: 20px;
          line-height: 1.2;
          color: #455A21;
          padding: 5px 5px 10px;
          background-color: #fff;
          border: 1px solid #fff;
          min-width: 175px;
          min-height: 56px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          border-radius: 50px;
          margin-bottom: 40px;

          &:hover {
            color: #fff;
            background-color: #455A21;
          }
        }

        p {
          color: #fff;
          font-size: 12px;
          line-height: 1.2;
          margin-bottom: 10px;
        }

        .order-info-box {
          display: flex;
          flex-wrap: wrap;
          margin-bottom: 20px;
          justify-content: space-between;
          align-items: center;

          .icon-area {
            flex: 0 0 10%;
            max-width: 10%;
          }

          .text-area {
            flex: 0 0 85%;
            max-width: 85%;
            text-align: left;

            p {
              font-family: 'Open Sans';
              color: #fff;
              font-size: 20px;
              line-height: 1.5;
              margin-bottom: 0;

              @media (max-width: 767.98px) {
                font-size: 16px;
              }

              a {
                color: inherit;
                text-decoration: underline;
              }

              strong {
                font-weight: bold;
              }
            }
          }
        }
      }
    }
  }
}