.home-banner {
  position: relative;
  background-size: cover;
  background-position: center;
  padding-top: 300px;
  padding-bottom: 200px;

  .content-area {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 585px;

    h2 {
      font-family: 'Crimson Text';
      font-weight: bold;
      font-size: 64px;
      line-height: 1.2;
      text-align: center;
      text-transform: uppercase;
      color: #fff;
      margin-bottom: 30px;
      width: 700px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;

      .large {
        font-size: 176px;
        line-height: 120px;
        display: block;
      }
    }

    p {
      width: 700px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      font-family: 'Open Sans';
      color: #fff;
      font-size: 20px;
      line-height: 1.8;
      text-align: center;
    }
  }
}

@media (max-width: 767.98px) {
  .home-banner {
    padding-top: 200px;
    padding-bottom: 100px;

    .content-area {
      min-height: unset;

      h2 {
        font-size: 40px;
        margin-bottom: 20px;

        .large {
          font-size: 90px;
          line-height: 1;
        }
      }

      p {
        font-size: 16px;
      }
    }
  }
}