.full-text {
  position: relative;
  padding-top: 70px;
  padding-bottom: 50px;

  @media (max-width: 767.98px) {
    padding-top: 40px;
    padding-bottom: 30px;
  }

  @media (min-width: 1200px) {

    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl {
      max-width: 1266px;
    }
  }

  .content-area {
    h3 {
      font-family: 'Crimson Text';
      font-size: 45px;
      font-weight: 600;
      margin-bottom: 15px;

      @media (max-width: 767.98px) {
        font-size: 30px;
      }
    }

    p {
      font-family: 'Open Sans';
      font-size: 20px;
      color: #4D4D4D;
      margin-bottom: 20px;

      @media (max-width: 767.98px) {
        font-size: 16px;
        margin-bottom: 15px;
      }
    }

    ul {
      padding-left: 20px;

      li {
        font-family: 'Open Sans';
        font-size: 20px;
        color: #4D4D4D;
        margin-bottom: 20px;
        padding-left: 10px;

        @media (max-width: 767.98px) {
          font-size: 16px;
          margin-bottom: 15px;
        }
      }
    }
  }
}