html {
  overflow-x: hidden;
}

body {
  font-family: 'Open Sans', sans-serif;

  &.scroll-lock {
    overflow: hidden;
    position: fixed;
  }

  @media (min-width: 1200px) {

    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container-xl {
      max-width: 1230px;
    }
  }

  a,
  button,
  input,
  select,
  textarea {
    transition: all .35s;

    &:focus,
    &:hover {
      box-shadow: none !important;
      outline: none !important;
      text-decoration: none;
      transition: all .35s;
    }
  }

  .btn-green {
    font-family: 'Crimson Text';
    font-weight: 600;
    font-size: 20px;
    line-height: 1.2;
    color: #fff;
    padding: 5px 5px 10px;
    background-color: #4C721D;
    border: 1px solid #4C721D;
    min-width: 175px;
    min-height: 56px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    border-radius: 50px;

    &:hover {
      color: #4C721D;
      background-color: #fff;
    }
  }

  .grey-bg {
    background-color: #F2F2F2;
  }

  .back-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;

    a {
      width: 50px;
      height: 50px;
      background-color: #4C721D;
      border-radius: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      pointer-events: none;

      img {
        width: 22.5px;
      }
    }

    &.active a {
      opacity: 1;
      pointer-events: all;
    }
  }

  .pdf-embed {
    margin-bottom: 65px;

    .content-area {
      .pdf-content {
        width: 850px;
        height: 1150px;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;

        @media (max-width: 767.98px) {
          height: unset;
        }
      }
    }
  }

  .table-strip-box {
    table {
      thead {
        tr {
          th {
            font-family: 'Open Sans';
            font-weight: bold;
            font-size: 20px;
            line-height: 1.5;
            color: #fff;
            text-align: center;
            vertical-align: middle;
            background-color: #4C721D;

            @media (max-width: 767.98px) {
              font-size: 12px;
            }
          }
        }
      }

      tbody {
        tr {
          background-color: #fff;

          &:nth-child(even) {
            background-color: #C7C7C8;
          }

          td {
            font-family: 'Open Sans';
            font-weight: normal;
            font-size: 20px;
            line-height: 1.5;
            color: #4D4D4D;
            text-align: center;
            padding: 15px;
            vertical-align: middle;

            @media (max-width: 767.98px) {
              font-size: 12px;
              padding: 5px;
            }

            &:first-child,
            &:last-child {
              text-align: center;
            }
          }
        }
      }

      &.no-strip {
        tbody tr:nth-child(even) {
          background-color: #fff;
        }
      }

      &.first-body-left-align tbody tr td:first-child {
        text-align: left;
      }
    }
  }

  .page-banner-image {
    background-size: cover;
    background-position: center;
    height: 515px;

    @media (max-width: 767.98px) {
      height: 250px;
    }
  }

}