footer {
  position: relative;
  padding-top: 65px;
  padding-bottom: 125px;
  background-color: #455A21;

  @media (max-width: 767.98px) {
    padding-top: 35px;
    padding-bottom: 75px;
  }

  .content-area {
    .footer-widget {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      .widget-box {
        flex: 0 0 100%;
        max-width: 100%;

        @media (max-width: 1199.98px) {
          margin-bottom: 50px;
        }

        &.quarter-size-smaller {
          flex: 0 0 22%;
          max-width: 22%;

          @media (max-width: 1199.98px) {
            flex: 0 0 50%;
            max-width: 50%;
          }

          @media (max-width: 767.98px) {
            flex: 0 0 100%;
            max-width: 100%;
          }
        }

        &.quarter-size {
          flex: 0 0 25%;
          max-width: 25%;

          @media (max-width: 1199.98px) {
            flex: 0 0 50%;
            max-width: 50%;
          }

          @media (max-width: 767.98px) {
            flex: 0 0 100%;
            max-width: 100%;
          }
        }

        &.half-size {
          flex: 0 0 50%;
          max-width: 50%;

          @media (max-width: 1199.98px) {
            flex: 0 0 100%;
            max-width: 100%;
          }
        }

        h3 {
          font-family: 'Open Sans';
          font-size: 24px;
          line-height: 1.2;
          font-weight: bold;
          color: #9BA221;
          margin-bottom: 30px;
        }

        p {
          font-family: 'Open Sans';
          font-size: 20px;
          line-height: 1.2;
          margin-bottom: 10px;
          color: #fff;

          a {
            color: inherit;
          }
        }

        ul.footer-menu {
          padding-left: 0;
          columns: 2;
          list-style: none;

          @media (max-width: 767.98px) {
            columns: 1;
          }

          li {
            margin-bottom: 10px;

            a {
              font-family: 'Open Sans';
              font-size: 20px;
              line-height: 1.2;
              color: #fff;
            }
          }
        }

      }
    }

    .footer-copyright {
      text-align: center;
      margin-top: 50px;

      @media (max-width: 767.98px) {
        margin-top: 30px;
      }

      p {
        font-family: 'Open Sans';
        font-size: 17px;
        line-height: 1.2;
        color: #fff;
      }
    }
  }
}