.team-list {
  position: relative;
  padding-top: 90px;
  padding-bottom: 40px;

  @media (max-width: 767.98px) {
    padding-top: 40px;
    padding-bottom: 10px;
  }

  .content-area {
    .title-area {
      h2 {
        font-family: 'Crimson Text';
        font-weight: 700;
        font-size: 45px;
        line-height: 1.2;
        margin-bottom: 30px;

        @media (max-width: 767.98px) {
          font-size: 30px;
        }
      }
    }

    .team-list-boxes {
      display: flex;
      flex-wrap: wrap;

      .team-list-box {
        flex: 0 0 32%;
        max-width: 32%;
        margin-right: 2%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 50px;

        &:nth-child(3n) {
          margin-right: 0;
        }

        @media (max-width: 991.98px) {
          flex: 0 0 49%;
          max-width: 49%;
          margin-right: 2%;

          &:nth-child(3n) {
            margin-right: 2%;
          }

          &:nth-child(even) {
            margin-right: 0;
          }
        }

        @media (max-width: 767.98px) {
          flex: 0 0 100%;
          max-width: 100%;
          margin-right: 0;

          &:nth-child(3n) {
            margin-right: 0;
          }

          &:nth-child(even) {
            margin-right: 0;
          }
        }

        .image-area {
          flex: 0 0 34%;
          max-width: 34%;

          .img-box {
            position: relative;
            width: 100%;
            padding-top: 100%;

            img {
              position: absolute;
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;
              object-fit: cover;
              object-position: center;
              border-radius: 50%;
            }
          }
        }

        .text-area {
          flex: 0 0 60%;
          max-width: 60%;

          h3 {
            font-family: 'Open Sans';
            font-weight: bold;
            font-size: 25px;
            line-height: 1;
          }

          h4 {
            font-family: 'Open Sans';
            font-size: 20px;
            line-height: 1.2;
          }
        }
      }
    }
  }
}