.image-4-column {
  position: relative;
  margin-top: 90px;
  margin-bottom: 90px;

  @media (max-width: 767.98px) {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .content-area {
    width: 1200px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;

    .image-area {
      flex: 0 0 24%;
      max-width: 24%;
      margin-right: 1.3%;
      margin-bottom: 1.3%;

      &:nth-child(4n) {
        margin-right: 0;
      }

      @media (max-width: 767.98px) {
        flex: 0 0 49%;
        max-width: 49%;
        margin-right: 2%;

        &:nth-child(even) {
          margin-right: 0;
        }
      }

      .image-box {
        position: relative;
        width: 100%;
        padding-top: 100%;
        border-radius: 20px;

        img {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          object-fit: cover;
          object-position: center;
        }
      }
    }
  }
}