.three-image-options {
  position: relative;
  margin-top: 150px;
  margin-bottom: 100px;

  @media (max-width: 767.98px) {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .content-area {
    display: flex;
    flex-wrap: wrap;

    .content-flex {
      flex: 0 0 32%;
      max-width: 32%;
      margin-right: 2%;
      margin-bottom: 10px;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-right: 0;
      }

      &:nth-child(3n) {
        margin-right: 0;
      }

      .content-box {
        position: relative;
        padding-top: 100%;
        background-size: cover;
        background-position: center;
        border-radius: 10px;
        overflow: hidden;

        &::before {
          content: "";
          background-color: rgba(0, 0, 0, .3);
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
        }

        .text-area {
          position: absolute;
          bottom: 25px;
          left: 25px;

          h3 {
            font-family: 'Crimson Text';
            font-weight: bold;
            font-size: 40px;
            line-height: 1.2;
            color: #fff;
            margin-bottom: 15px;
            padding-right: 10%;
          }

          a {
            font-family: 'Open Sans';
            font-weight: bold;
            font-size: 20px;
            line-height: 1.2;
            color: #455A21;
            background-color: #fff;
            min-width: 160px;
            min-height: 50px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            padding: 5px;
            border-radius: 10px;

            &:hover {
              color: #fff;
              background-color: #455A21;
            }
          }
        }
      }
    }
  }
}