.full-column-icon {
  position: relative;
  padding-top: 90px;

  .column-content-area {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: space-between;
    width: 1070px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;

    .content-box {
      flex: 0 0 100%;
      max-width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-bottom: 65px;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 30px;
        text-align: left;
      }

      .icon-image {
        margin-bottom: 25px;
        flex: 0 0 14%;
        max-width: 14%;

        img {
          width: 125px;

          @media (max-width: 991.98px) {
            max-width: 80%;
          }
        }
      }

      .text-area {
        flex: 0 0 85%;
        max-width: 85%;

        h3 {
          font-family: 'Crimson Text';
          font-size: 40px;
          font-weight: 600;
          line-height: 1.2;
          margin-bottom: 5px;
          color: #000000;

          @media (max-width: 767.98px) {
            font-size: 28px;
          }
        }

        p {
          font-family: 'Open Sans';
          font-size: 20px;
          line-height: 1.8;
          color: #4D4D4D;

          a {
            color: #4C721D;
            text-decoration: underline;
          }

          @media (max-width: 767.98px) {
            font-size: 16px;
          }
        }

        ul {
          padding-left: 15px;
          list-style: none;

          li {
            color: #4D4D4D;
            font-family: 'Open Sans';
            font-size: 20px;
            line-height: 1.6;
            margin-bottom: 10px;

            &::before {
              content: "\2022";
              color: #4C721D;
              font-weight: bold;
              display: inline-block;
              width: 15px;
              margin-left: -15px;
            }

            @media (max-width: 767.98px) {
              font-size: 16px;
            }

            a {
              color: #4C721D;
              text-decoration: underline;
            }
          }
        }
      }

    }
  }

  .form-get-started {
    position: relative;
    background-color: #fff;
    margin-top: 100px;
    box-shadow: 0 0 30px rgba(0, 0, 0, .1);
    padding: 55px 75px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;

    @media (max-width: 767.98px) {
      padding: 25px;
      margin-top: 30px;
    }

    .text-area {
      flex: 0 0 45%;
      max-width: 45%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      h3 {
        font-family: 'Crimson Text';
        font-size: 40px;
        font-weight: 600;
        line-height: 1.2;
        margin-bottom: 15px;
        color: #000;

        @media (max-width: 767.98px) {
          font-size: 28px;
        }
      }

      p {
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 1.8;
        color: #4D4D4D;

        @media (max-width: 767.98px) {
          font-size: 16px;
        }
      }
    }

    .form-area {
      flex: 0 0 45%;
      max-width: 45%;

      @media (max-width: 991.98px) {
        flex: 0 0 100%;
        max-width: 100%;
      }

      form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;

        .input-area {
          flex: 0 0 100%;
          max-width: 100%;
          margin-bottom: 20px;

          @media (max-width: 767.98px) {
            margin-bottom: 10px;
          }

          &.half-width {
            flex: 0 0 48%;
            max-width: 48%;
          }

          input {
            width: 100%;
            border: 1px solid #333;
            border-radius: 5px;
            font-family: 'Open Sans';
            font-size: 16px;
            line-height: 1;
            color: #333;
            padding: 15px;

            @media (max-width: 767.98px) {
              padding: 10px;
            }

            &.name-input {
              background-image: url(../../images/form-name-icon.jpg);
              background-size: auto 30%;
              background-repeat: no-repeat;
              background-position: center right 10px;
              padding-right: 40px;
            }
          }
        }

        .input-submit {
          flex: 0 0 100%;
          max-width: 100%;
          margin-top: 20px;

          @media (max-width: 767.98px) {
            margin-top: 10px;
          }

          input {
            font-family: 'Crimson Text';
            font-size: 20px;
            font-weight: 600;
            min-width: 175px;
            min-height: 56px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            background-color: #4C721D;
            border: 1px solid #4C721D;
            border: none;
            border-radius: 50px;
            color: #fff;
            padding: 5px 5px 8px;

            @media (max-width: 767.98px) {
              font-size: 16px;
              min-width: 150px;
              min-height: 45px;
            }

            &:hover {
              color: #4C721D;
              background-color: #fff;
              border: 1px solid #4C721D;
            }
          }
        }
      }
    }
  }
}